import React from 'react'
import { useLoaderData } from 'react-router-dom';

// project
import CustomizedAvatarWithUser from 'components/components/CustomizedAvatarWithUserAndDate'
import { Box, Divider, Stack, Typography } from '@mui/material';
import CustomizedImage from 'components/components/CustomizedImage';
import CustomizedComment from 'components/components/CustomizedComment';
import CustomizedCommentSend from 'components/components/CustomizedComment/CommenSend';
import { Parser } from 'html-to-react';
import PageContainer from '../../layouts/PageContainer';
import PageHeading from '../../layouts/PageHeading';

// mocks
import comments from '__mocks__/comments'
import {CONTENT as content} from '__mocks__/html-content'

export default function BlogDetail(props) {
  const post = useLoaderData();
  return (
    <PageContainer>
      <PageHeading title="Blog Detail" back />
      <Box sx={{ p: 3 }}>
        <Stack spacing={3}>
          <Typography variant='h1' gutterBottom>
            {post.title}
          </Typography>

          <Typography variant='4' gutterBottom>
            {post.paragraph}
          </Typography>

          <CustomizedAvatarWithUser name={post.author.name} createAt={post.createAt} avatar={post.author.avatarUrl} sx={{ my: 2 }} />

          <CustomizedImage url={post.cover} sx={{ width: 800, height: 540, margin: 'auto' }} />
          <Box component='div' sx={{ p: 2, fontSize: 18 }}>
            {Parser().parse(content)}
          </Box>
          <Divider />

          <CustomizedCommentSend author={{ avatarUrl: post.author.avatarUrl }} />

          {comments.map((comment, index) => (
            <CustomizedComment {...comment} />
          ))}
        </Stack>
      </Box>
    </PageContainer>
  )
}
